.App .content {
  width: 100%;
  max-width: 800px;
  margin: auto;
  background: rgba(251, 233, 64, 0.75);
  text-align: center;
  border: 3px solid #000;
  padding: 30px;
  margin-top: 50px;
}

.App .content h1 {
  font-size: 3em;
  text-transform: uppercase;
  font-weight: bolder;
  font-family: "Raleway", sans-serif;
}

.App .minions span {
  font-size: 10em;
  display: block;
}
.App .minions {
  font-weight: bold;
}

.App .message {
  background: rgba(251, 233, 64, 0.75);
  font-size: 1.4em;
  width: max-content;
  margin: 30px auto;
  padding: 10px;
  border-radius: 10px;
}

.App .result {
  display: flex;
  justify-content: space-between;
}
